<template lang="pug">
div(v-if="showit" @mouseenter="hovering(true)" @mouseleave="hovering(false)").has-text-centered
    component(:is="theAnimal" :id="customid" :class="hover ? 'animated tada' : 'animated rubberBand fast'").animal.animated.rubberBand
</template>
<script>
import {Howl, Howler} from 'howler';
export default {
    props: ['name'],

    data () {
        return {
           customid: null,
           showit: false,
           hover: false,
           startanim: {
               classes: 'tada',
               delay: 500
           },
           sound: new Howl({ src: [require('@/assets/sounds/enter.mp3')] })
        }
    },
    computed : {
        theAnimal () {
            return () => import('@/assets/animals/'+this.name+'.svg')
        }
    },
    methods: {
        soundit () {
            if (!this.sound.playing()) {
                this.sound.play()
            }
        },
        hovering (state) {
            this.hover = state
            if (state) { this.soundit()}
        },
        animduration (customid) {
            var _this = this
            setTimeout(function () {
                _this.showit = true
            }, 200)
            setTimeout(function () {
                var cabeza = document.getElementById(customid)
                var ojos = cabeza.querySelector('.ojos')
                ojos.style.setProperty('animation-duration', (Math.random()*3)+2 +'s')
            }, 1000)
        },
        clicked () {
            this.soundit()

        }
    },
    mounted () {
        //SE ANIMATION ID
        this.customid = 'id'+Math.floor(Math.random()*100)
        //RUN ANINMATION ON CREATED
        this.animduration(this.customid)
        //SOUND volume
        Howler.volume(0.4)

    }
}
</script>
<style lang="stylus" scoped>
.ojos
    animation-name: blink
    animation-iteration-count: infinite
@keyframes blink
    0%
        opacity: 1
        transform translateY(0px)
    95%
        opacity: 1
        transform translateY(0px)
    98%
        opacity: 0
        transform translateY(10px)
    100%
        opacity: 1
        transform translateY(0px)
</style>