
<template lang="pug">
  #app
    template(v-if="current==4"): .ganaste: .dancer
    template(v-else)
      .start.is-overlay(v-if="!started"): .btn.animated.jello.infinite.slower(@click="start"): Start
      .oknote(:class="okanim ? 'active' : ''")
      .section.marcador: .container
        .columns.is-mobile
          .column: .check(:class="okeis>0 ? 'active' : ''"): ion-icon(name="checkmark" v-pre)
          .column: .check(:class="okeis>1 ? 'active' : ''"): ion-icon(name="checkmark" v-pre)
          .column: .check(:class="okeis>2 ? 'active' : ''"): ion-icon(name="checkmark" v-pre)
          .column: .check(:class="okeis>3 ? 'active' : ''"): ion-icon(name="checkmark" v-pre)
      .section.container.animals
        .columns.is-vcentered.is-mobile.is-multiline.animals
          .column.is-4(v-for="(li, index) in list" :key="index" @click="selected(li)"): Animal(:name="li")
      template(v-if="current==0"): Bocina(:name="sounds[0]+'.mp3'" ref="chickensound")
      template(v-if="current==1"): Bocina(:name="sounds[1]+'.mp3'" ref="tigersound")
      template(v-if="current==2"): Bocina(:name="sounds[2]+'.mp3'" ref="pigsound")
      template(v-if="current==3"): Bocina(:name="sounds[3]+'.mp3'" ref="bearsound")
      .info(@click="info") ?

</template>

<script>
import 'bulma/css/bulma.css'
import "animate.css/animate.css";
import Animal from './components/Animal.vue'
import Bocina from './components/Bocina.vue'
import Start from '@/assets/start.svg'
import {Howl} from 'howler';
export default {
  name: 'App',
  components: {
    Animal,
    Bocina,
    Start
  },
  data () {
    return {
      list: ['bear', 'koala', 'chicken', 'tiger', 'pig', 'monkey', 'frog', 'panda', 'fox'],
      sounds: ['chicken', 'tiger', 'pig', 'bear'],
      current: null,
      started: false,
      bgsound: null,
      infosound: null,
      oksound: null,
      errorsound: null,
      okeis: 0,
      okanim: false
    }
  },
  methods: {
    start () {
      this.started = true
      this.current = 0
      this.bgsound = new Howl({
        src: [require('@/assets/sounds/bg.mp3')],
        autoplay: true,
        loop: true,
        volume: 0.3
      })
      this.info ()
      this.oksound = new Howl({
        src: [require('@/assets/sounds/ok.mp3')],
      })
      this.errorsound = new Howl({
        src: [require('@/assets/sounds/error.mp3')],
      })

    },
    info () {
      this.infosound = new Howl({
        src: [require('@/assets/sounds/info.mp3')],
      })
      this.infosound.play()
    },
    selected (name) {
      if (name == this.sounds[this.current]) {
        // IF OK !!!!
        var _this = this
        this.oksound.play()
        this.current++
        this.okeis++
        this.okanim = true
        if(this.current < 4) {
          setTimeout(function () {
            _this.okanim = false
            var currentsound = _this.sounds[_this.current]+'sound'
            console.log(currentsound)
            _this.$refs[currentsound].setsound()
          }, 4000)
        } else {
          console.log('YOUWIN')
        }

      } else {
        this.errorsound.play()
      }
    }
  },
}
</script>

