<template lang="pug">
    div(:class="active ? 'bocina active' : 'bocina'" @click="setsound").animated.zoomInLeft: .sonando: .waves: ion-icon(name="musical-notes" v-pre)
</template>
<script>
import {Howl} from 'howler';
export default {
    props: ['name'],
    data () {
        return {
            sound: null,
            active: true
        }
    },
    methods: {
        setsound () {
            var _this = this
            _this.active = true
            var sound =  new Howl({
                src: [require('@/assets/sounds/'+this.name)],
                onend: function() {
                    _this.active = false
                }
            })
            sound.play()
        }
    },
    mounted () {
        this.active = false
        this.setsound()
    }
}
</script>
<style lang="stylus" scoped>
.bocina
    position fixed
    bottom 40px
    left 40px
    z-index 50
    .sonando
        width 120px
        height 120px
        background: #446068
        border-radius: 50%
        border 8px solid #859BA2
        box-sizing border-box
        display flex
        justify-content center
        align-items center
        cursor: pointer
    .waves
        width 40px
        height 40px
        display flex
        justify-content center
        align-items center
.bocina:not(.active)
    .sonando
        background: linear-gradient(#EF5A85, #EF2085)
        border 8px solid #F9A667
        animation 2s boof infinite
.bocina.active
    .sonando
        animation 0.5s boof infinite
        .waves
            border 4px solid #96B7C3
            opacity .5
            border-radius 50%
            animation .5s waveboof infinite
@keyframes boof
    0%
        border 4px solid #859BA2
        transform scale(1)
    25%
        border 36px solid #859BA2
        transform scale(0.7)
    100%
        border 4px solid #859BA2
        transform scale(1)
@keyframes waveboof
    0%
        transform scale(1)
        border 4px solid #96B7C3
        opacity .5
    100%
        transform scale(5)
        border 1px solid #446069
        opacity 0
ion-icon
    color #fff
</style>